<template>
  <div class="register">
    <div>
      <van-nav-bar
        title="用户注册"
        @click-left="onClickLeft"
      >
        <van-icon
          name="arrow-left"
          slot="left"
          size="20px"
        />
      </van-nav-bar>
    </div>
    <div>
      <van-field
        v-model="username"
        required
        label="用户名"
        placeholder="请输入用户名"
        :error="userError"
        @blur="userNameCheck"
        @focus="userError=false"
        size="large"
        clearable
      />

      <van-field
        v-model="password"
        type="password"
        label="密码"
        placeholder="请输入密码"
        required
        :error="passwordError"
        @blur="passwordCheck"
        @focus="passwordError=false"
        size="large"
        clearable
      />
        
      <van-field
        v-model="phone"
        type="tel"
        label="电话"
        placeholder="请输入电话"
        required
        :error="phoneError"
        @blur="phoneCheck"
        @focus="phoneError=false"
        size="large"
        clearable
      />

      <van-field
        v-model="address"
        label="地址"
        placeholder="请输入地址"
        required
        :error="addressError"
        @blur="addressCheck"
        @focus="addressError=false"
        size="large"
        clearable
      />

      <van-field
        v-model="name"
        label="姓名"
        placeholder="你的真实姓名"
        size="large"
        clearable
      />
        
      <van-field
        v-model="sex"
        label="性别"
        placeholder="你的性别"
        size="large"
        clearable
      />

      <van-field
        v-model="qq"
        label="QQ"
        type="number"
        placeholder="请输入QQ"
        size="large"
        clearable
      />
    </div>
    <div style="text-align:center">
      <Button
        type="primary"
        size="large"
        @click="click"
        style="width: 90%;margin-top:30px;"
        :disabled="buttonDisabled"
      >
        提交
      </Button>
    </div>
  </div>
</template>

<script>
    import Vue from 'vue';
    import { Field,NavBar,Toast,Dialog,Icon } from 'vant';
    import { Button } from 'view-design';

    Vue.use(Field,Toast);
    Vue.use(NavBar);
    Vue.use(Icon);
    Vue.component('Button', Button);

    export default {
        name: 'Register',
        data() {
            return {
                buttonDisabled: true,
                userError: false,
                passwordError: false,
                phoneError: false,
                addressError: false,
                password:'',
                username:'',
                name:'',
                address:'',
                phone:'',
                qq:'',
                sex:'',
            }
        },
        updated() {
            this.check()
        },
        methods: {
            onClickLeft() {
                this.$router.go(-1);
            },
            check(){  // 有空的话提交按钮禁用
                if(this.username==''||this.password==''||this.phone==''||this.address==''){
                    this.buttonDisabled=true;
                }else{
                    this.buttonDisabled=false;
                }
            },
            click(){
                //用post向后台提交注册信息  ======未完成：注册输入框判断======
                Dialog.alert({
                    title: '提示',
                    message: '注册成功！'  // 返回一个状态码，为200或者success的时候提示注册成功
                }).then(() => {
                    this.infoSub();
                    this.$router.go(-1);
                });
            },
            userNameCheck(){   
                if(this.username==''){
                    this.userError=true
                }else{
                    this.userError=false
                }
            },
            passwordCheck(){   
                if(this.password==''){
                    this.passwordError=true
                }else{
                    this.passwordError=false
                }
            },
            phoneCheck(){   
                if(this.phone==''){
                    this.phoneError=true
                }else{
                    this.phoneError=false
                }
            },
            addressCheck(){   
                if(this.address==''){
                    this.addressError=true
                }else{
                    this.addressError=false
                }
            },
            infoSub(){
                Toast('提交信息')
            }
        },
    }
</script>

